@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital@0;1&display=swap');
// font-family: 'Open Sans', sans-serif;

@font-face {
	font-family: 'GothamBook';
	src: url('/fonts/GothamBook/GothamBook.eot');
	src: local('GothamBook'), url('/fonts/GothamBook/GothamBook.woff') format('woff'), url('/fonts/GothamBook/GothamBook.ttf') format('truetype');
}
// font-family: 'GothamBook', 'Open Sans', sans-serif;
@font-face {
	font-family: 'GothamBold';
	src: url('/fonts/GothamBold/GothamBold.eot');
	src: local('GothamBold'), url('/fonts/GothamBold/GothamBold.woff') format('woff'), url('/fonts/GothamBold/GothamBold.ttf') format('truetype');
}
// font-family: 'GothamBold', 'Open Sans', sans-serif;
@font-face {
	font-family: 'GothamMedium';
	src: url('/fonts/GothamMedium/GothamMedium.eot');
	src: local('GothamMedium'), url('/fonts/GothamMedium/GothamMedium.woff') format('woff'), url('/fonts/GothamMedium/GothamMedium.ttf') format('truetype');
}
// font-family: 'GothamMedium', 'Open Sans', sans-serif;

